import {Injectable} from '@angular/core';
import {Http} from "@angular/http";
import {AlertController} from "ionic-angular";
import {FileTransfer, FileTransferObject} from '@ionic-native/file-transfer';
import {FileOpener} from '@ionic-native/file-opener';
import {File} from '@ionic-native/file';
import {UtilServiceProvider} from "../util-service/util-service";
import {GlobalConfigProvider} from "../global-config/global-config";
import {map} from "rxjs/operators";

/**
 * 
 */
@Injectable()
export class UpdateProvider {
    appName; // app name
    appType; // android  ios
    currentVersionNo; // 
    latestVersionNo; // 
    lastVersionInfo; // app
    versions; // app

    appDownloadPageUrl = 'https://fir.im/emzu'; // 
    iosUrl; // ios 
    apkUrl; // android apk

    // app.0,app
    updateProgress = -1;

    constructor(public transfer: FileTransfer,
                public file: File,
                public utilService: UtilServiceProvider,
                public fileOpener: FileOpener,
                public http: Http,
                public globalConfig: GlobalConfigProvider,
                public alertCtrl: AlertController) {

    }

    checkVersion() {
        if (!this.utilService.isMobile()) {
            return;
        }

        // app
        this.utilService.getVersionNumber().subscribe(
            currentVersionNo => {
                this.currentVersionNo = currentVersionNo;
                this.http.get(this.globalConfig.APP_SERVE_URL + 'admin/get/version')
                    .pipe(map(res => res.json()))
                    .subscribe(res => {
                        if (!res || res.code != 0) {
                            console.log('');
                            return;
                        }

                        if (res.code == 0 && res.data && !res.data.versionNumber) {
                            console.log('');
                            return;
                        }

                        const data = res.data;
                        this.lastVersionInfo = data.version;
                        this.latestVersionNo = data.versionNumber;
                        this.iosUrl = data.iosUrl;
                        this.apkUrl = data.androidUrl;

                        if (this.latestVersionNo && (this.currentVersionNo == this.latestVersionNo)) {
                            console.log('');
                            return;
                        }

                        const cNo = parseInt(this.currentVersionNo.replace(/\./g, ''), 10);
                        const lNo = parseInt(this.latestVersionNo.replace(/\./g, ''), 10);
                        if(cNo < lNo){
                            const that = this;
                            let alert = this.alertCtrl.create({
                                title: '',
                                subTitle: ',',
                                enableBackdropDismiss: false,
                                buttons: [{
                                    text: '', handler: () => {
                                        that.downloadApp();
                                    }
                                }]
                            });
                            alert.present();
                        }
                    }, err => {
                        console.log(err, '');
                    });
            }
        )

    }

    /**
     * app
     */
    downloadApp() {
        if (this.utilService.isIos()) {
            // ios
            console.log(this.iosUrl);
            this.utilService.openUrlByBrowser(this.iosUrl);
        }

        if (this.utilService.isAndroid()) {// android
            if (!this.apkUrl) {
                this.utilService.alert(this.alertCtrl,'android apk');
                return;
            }

            this.utilService.externalStoragePermissionsAuthorization().subscribe(() => {
                let backgroundProcess = false; // 

                let alert; // 
                alert = this.alertCtrl.create({
                    title: '0%',
                    enableBackdropDismiss: false,
                    buttons: [{
                        text: '', handler: () => {
                            backgroundProcess = true;
                        }
                    }]
                });
                alert.present();

                // apk
                const fileTransfer: FileTransferObject = this.transfer.create();
                const apk = this.file.externalRootDirectory + 'download/' + `android_${new Date().getTime()}.apk`; // apk
                console.log(this.apkUrl);
                fileTransfer.download(this.apkUrl, apk).then((entry) => {
                    alert && alert.dismiss();
                    console.log('download complete: ' + entry.toURL());
                    // apk
                    this.fileOpener.open(entry.toURL(), 'application/vnd.android.package-archive');
                }, err => {
                    this.updateProgress = -1;
                    alert && alert.dismiss();
                    console.log(err, 'android app ');
                    this.alertCtrl.create({
                        title: '',
                        subTitle: '',
                        buttons: [{
                            text: '', handler: () => {
                                this.utilService.openUrlByBrowser(this.appDownloadPageUrl); // 
                            }
                        }
                        ]
                    }).present();
                });

                // 
                let timer = null; // onProgress,setTimeout
                fileTransfer.onProgress((event: ProgressEvent) => {
                    const progress = Math.floor(event.loaded / event.total * 100); // 
                    this.updateProgress = progress;
                    if (!timer) {
                        // 
                        timer = setTimeout(() => {
                            if (progress === 100) {
                                alert && alert.dismiss();
                            } else {
                                if (!backgroundProcess) {
                                    const title = document.getElementsByClassName('alert-title')[0];
                                    title && (title.innerHTML = `${progress}%`);
                                }
                            }
                            clearTimeout(timer);
                            timer = null;
                        }, 1000);
                    }
                });
            });
        }
    }

}



