import {Injectable} from '@angular/core';
import {AlertController, Loading, LoadingController, Platform, Toast, ToastController} from "ionic-angular";
import {InAppBrowser} from "@ionic-native/in-app-browser";
import {Diagnostic} from "@ionic-native/diagnostic";
import {Observable} from "rxjs/Observable";
import {AppVersion} from "@ionic-native/app-version";
// index.html
declare var AlloyLever;

/**
 * 
 */
@Injectable()
export class UtilServiceProvider {
    constructor(public inAppBrowser: InAppBrowser,
                public diagnostic: Diagnostic,
                public platform: Platform,
                public appVersion: AppVersion,
                public alertCtrl: AlertController) {

    }

    /**
     *  loading 
     *
     * @protected
     * @param {LoadingController} loadingCtrl
     * @param {string} message
     * @returns {Loading}
     * @memberof BaseUI
     */
    showLoading(loadingCtrl: LoadingController, message?: string): Loading {
        let loader = loadingCtrl.create({
            content: message,
            dismissOnPageChange: true // loading
        });
        loader.present();
        return loader;
    }


    /**
     *  toast 
     *
     * @protected
     * @param {ToastController} toastCtrl
     * @param {string} message
     * @returns {Toast}
     * @memberof BaseUI
     */
    showToast(toastCtrl: ToastController, message: string): Toast {
        let toast = toastCtrl.create({
            message: message,
            duration: 3000, //
            position: 'bottom'
        });
        toast.present();
        return toast;
    }

    /**
     * @param message
     * @param callback
     */
    alert(alertCtrl: AlertController, message, callback?) {
        if (callback) {
            let alert = alertCtrl.create({
                title: '',
                message: message,
                buttons: [
                    {
                        text: '',
                        handler: data => {
                            console.log('Cancel clicked');
                        }
                    },
                    {
                        text: "",
                        handler: data => {
                            callback();
                        }
                    }]
            });
            alert.present();
        } else {
            let alert = alertCtrl.create({
                title: '',
                message: message,
                buttons: [""]
            });
            alert.present();
        }
    }

    /**
     * AlloyLever,""
     * :https:// github.com/AlloyTeam/AlloyLever
     */
    alloyLeverInit() {
        AlloyLever.config({
            cdn: 'http://s.url.cn/qqun/qun/qqweb/m/qun/confession/js/vconsole.min.js',  // vconsoleCDN
            /*reportUrl: "// a.qq.com",  // 
            reportPrefix: 'qun',    // msg
            reportKey: 'msg',        // msgkeymsg
            otherReport: {              // 
              uin: 491862102
            },*/
            entry: '#entry'         // DOM6vConsole// AlloyLever.entry('#entry2')
        });
        AlloyLever.entry('#entry2');
    }

    /**
     * url
     */
    openUrlByBrowser(url: string): void {
        this.inAppBrowser.create(url, '_system');
    }

    /**
     * 
     */
    isMobile(): boolean {
        return this.platform.is('mobile') && !this.platform.is('mobileweb');
    }

    /**
     * android
     */
    isAndroid(): boolean {
        return this.isMobile() && this.platform.is('android');
    }

    /**
     * ios
     */
    isIos(): boolean {
        return this.isMobile() && (this.platform.is('ios') || this.platform.is('ipad') || this.platform.is('iphone'));
    }

    /**
     * app,0.01
     * @description  /config.xmlversion
     */
    getVersionNumber(): Observable<string> {
        return Observable.create(observer => {
            this.appVersion.getVersionNumber().then((value: string) => {
                observer.next(value);
            }).catch(err => {
                console.log(err, 'app');
                observer.error(false);
            });
        });
    }

    /**
     * app/id,com.kit.ionic2tabs
     * @description  /config.xmlid
     */
    getPackageName(): Observable<string> {
        return Observable.create(observer => {
            this.appVersion.getPackageName().then((value: string) => {
                observer.next(value);
            }).catch(err => {
                console.log(err, 'app');
                observer.error(false);
            });
        });
    }

    /**
     * sequence1
     * @type {()=>number}
     */
    getSequence = (() => {
        // let sequence = 1;
        // return () => {
        //     return ++sequence;
        // };
        return new Date().getTime();
    })();

    /**
     * app,
     */
    externalStoragePermissionsAuthorization = (() => {
        let havePermission = false;
        return () => {
            return Observable.create(observer => {
                if (havePermission) {
                    observer.next(true);
                } else {
                    const permissions = [this.diagnostic.permission.READ_EXTERNAL_STORAGE, this.diagnostic.permission.WRITE_EXTERNAL_STORAGE];
                    this.diagnostic.getPermissionsAuthorizationStatus(permissions).then(res => {
                        if (res.READ_EXTERNAL_STORAGE == 'GRANTED' && res.WRITE_EXTERNAL_STORAGE == 'GRANTED') {
                            havePermission = true;
                            observer.next(true);
                        } else {
                            havePermission = false;
                            this.diagnostic.requestRuntimePermissions(permissions).then(res => {// 
                                if (res.READ_EXTERNAL_STORAGE == 'GRANTED' && res.WRITE_EXTERNAL_STORAGE == 'GRANTED') {
                                    havePermission = true;
                                    observer.next(true);
                                } else {
                                    havePermission = false;
                                    this.alertCtrl.create({
                                        title: '',
                                        subTitle: 'app',
                                        buttons: [{text: ''},
                                            {
                                                text: '',
                                                handler: () => {
                                                    this.diagnostic.switchToSettings();
                                                }
                                            }
                                        ]
                                    }).present();
                                    observer.error(false);
                                }
                            }).catch(err => {
                                console.log(err, 'diagnostic.requestRuntimePermissions');
                                observer.error(false);
                            });
                        }
                    }).catch(err => {
                        console.log(err, 'diagnostic.getPermissionsAuthorizationStatus');
                        observer.error(false);
                    });
                }
            });
        };
    })();
}



